import type { ConvertedMetadata, EntitySet, EntityType, BaseNavigationProperty } from '@sap-ux/vocabularies-types';
import type { XMLElement } from '@xml-tools/ast';
import type { SchemaDefinition } from '@sap/ux-specification-types';
import { UIVOCABULARY } from '@sap/ux-specification-types';
import type { CustomPageConnection } from '../../types';
import { addSelectionFields } from '../listReport';
import { findAlias } from '../../../common';
import { getAttribute } from './utils';

interface HandlerParams {
    serviceAVT: ConvertedMetadata;
    node: XMLElement;
    // Current parsing context schema
    schema: SchemaDefinition;
    // Full schema
    rootSchema: SchemaDefinition;
    connectionContext: CustomPageConnection;
}

type AnnotationMacrosHandler = (params: HandlerParams) => void;

/**
 * Method returns entity type for passed macros annotation handler params.
 * @param {HandlerParams} params Macros annotation handler params containing serviceAvt and connection context information.
 * @returns {EntityType | undefined} EntityType for passed connection.
 */
function getEntityTypeResolution(params: HandlerParams): EntityType | undefined {
    const { node, serviceAVT, connectionContext } = params;
    const contextPath = getAttribute(node, 'contextPath') || connectionContext.contextPath;
    const entitySet = connectionContext.entitySet;
    if (!contextPath && !entitySet) {
        return undefined;
    }
    const resolvedContextPath = serviceAVT?.resolvePath<EntitySet | EntityType | BaseNavigationProperty>(
        contextPath || `/${entitySet}`
    );
    let entityType: EntityType | undefined;
    switch (resolvedContextPath?.target?.['_type']) {
        case 'EntitySet': {
            entityType = resolvedContextPath.target.entityType;
            break;
        }
        case 'NavigationProperty': {
            entityType = resolvedContextPath.target.targetType;
            break;
        }
        case 'EntityType': {
            entityType = resolvedContextPath.target;
            break;
        }
    }
    return entityType;
}

/**
 * Method applies annotation path for passed XML node's schema.
 * @param {HandlerParams} schema Schema to update with "annotationPath".
 * @param {XMLElement} node XML node of macro element.
 * @param {EntityType} [entityType] Entity type object.
 */
function applyAnnotationPath(schema: SchemaDefinition, node: XMLElement, entityType?: EntityType): void {
    const metaPath = getAttribute(node, 'metaPath');
    if (metaPath && entityType) {
        schema.annotationPath = `/${entityType.fullyQualifiedName}/${metaPath}`;
    }
}

// Handlers map for macros controls
export const annotationMacrosHandlers: { [key: string]: AnnotationMacrosHandler } = {
    'sap.fe.macros.Chart': (params: HandlerParams) => {
        const entityType = getEntityTypeResolution(params);
        applyAnnotationPath(params.schema, params.node, entityType);
    },
    'sap.fe.macros.FilterBar': (params: HandlerParams) => {
        const { schema, serviceAVT, rootSchema } = params;
        const entityType = getEntityTypeResolution(params);
        // Handler selection fields
        const filterFieldsDefinition = schema.properties?.filterFields;
        if (typeof filterFieldsDefinition === 'object' && entityType) {
            if (!filterFieldsDefinition.properties) {
                filterFieldsDefinition.properties = {};
            }
            const alias = findAlias(UIVOCABULARY, serviceAVT);
            const selectionFieldAnnotation = alias && entityType.annotations?.[alias]?.SelectionFields;
            addSelectionFields(entityType, rootSchema, selectionFieldAnnotation, filterFieldsDefinition);
        }
    }
};
