import { xml2js } from 'xml-js';
import type { Element } from 'xml-js';
import { buildAst } from '@xml-tools/ast';
import type { XMLDocument } from '@xml-tools/ast';
import { parse } from '@xml-tools/parser';
import type { DocumentCstNode } from '@xml-tools/parser';
import i18next from 'i18next';
import type { MacrosXMLPath, ExtensionLogger, FileData, SchemaDefinition } from '@sap/ux-specification-types';
import { LogSeverity } from '@sap/ux-specification-types';
import { getDefinitionKey, getErrorMessage } from '../../common/utils';
import { log } from '../../../extensionLogger';

export interface TraverseData {
    fullSchema: SchemaDefinition;
    schema: SchemaDefinition;
    element: Element;
    contextPath: MacrosXMLPath;
    config: object;
    parent?: TraverseData;
}

export type TraverseCallback = (
    traverseData: TraverseData,
    property: SchemaDefinition,
    config: object,
    name: string
) => boolean;

/**
 * Method finds element by XML path.
 * @param {Element} element Element to look in.
 * @param {MacrosXMLPath} path XML path to search.
 * @returns {Element | undefined} Element is returned if element found by passed path.
 */
function findElementByXMLPath(element: Element, path: MacrosXMLPath): Element | undefined {
    let currentElement: Element | undefined = element;
    for (const subPath of path) {
        currentElement = currentElement.elements?.[subPath.index];
    }
    return currentElement;
}

/**
 * Method ensures that inner object exists for passed property name.
 * @param {object} obj Object to check and update.
 * @param {string} name Property name in object to check and update.
 * @returns {boolean} True if object created.
 */
function ensureObjectExists(obj: object, name: string): boolean {
    if (!obj[name]) {
        obj[name] = {};
        return true;
    }
    return false;
}

/**
 * Method traverses schema together with XML element.
 * @param {TraverseData} traverseData Traverse data object.
 * @param {TraverseCallback} callback Callbck to handle export or import.
 * @returns {boolean} Is changes applied during export or import.
 */
export function traverseSchema(traverseData: TraverseData, callback: TraverseCallback): boolean {
    const { schema, element, contextPath, config, fullSchema } = traverseData;
    let { properties = {} } = schema;
    let differs = false;
    if (schema.$ref && !schema.properties) {
        const key = getDefinitionKey(schema.$ref);
        const definition = fullSchema.definitions[key];
        if (typeof definition === 'object' && definition.properties) {
            properties = definition.properties;
        }
    }
    for (const name in properties) {
        const property = properties[name];
        if (typeof property !== 'object') {
            continue;
        }
        // Check if object contains "metadata" to map with XML element
        if (property['metadata']?.['path']?.length) {
            const path = property['metadata']['path'];
            const checkPath = path.slice(contextPath.length);
            const targetElement = findElementByXMLPath(element, checkPath);
            differs = ensureObjectExists(config, name) || differs;
            const contextConfig = config[name];
            differs =
                traverseSchema(
                    {
                        fullSchema: traverseData.fullSchema,
                        schema: property,
                        element: targetElement,
                        contextPath: path,
                        config: contextConfig,
                        parent: traverseData
                    },
                    callback
                ) || differs;
        } else {
            differs = callback(traverseData, property, config, name) || differs;
        }
    }
    return differs;
}

/**
 * Method parses XML file and returns parsed object.
 * @param {FileData} xmlFile XML file.
 * @param {boolean} providePositions - If true then parsing result will contain information about elements and attributes positions.
 * @param {ExtensionLogger} [logger] - Logger class for logging messages.
 * @returns {Element | undefined} Parsed XML.
 */
export function parseXML(xmlFile: FileData, providePositions: true, logger?: ExtensionLogger): XMLDocument | undefined;
export function parseXML(xmlFile: FileData, providePositions: false, logger?: ExtensionLogger): Element | undefined;
export function parseXML(
    xmlFile: FileData,
    providePositions: boolean,
    logger?: ExtensionLogger
): Element | XMLDocument | undefined {
    let element: Element | XMLDocument;
    try {
        if (providePositions) {
            const { cst, tokenVector } = parse(xmlFile.fileContent);
            const xmlDocument = buildAst(cst as DocumentCstNode, tokenVector);
            if (!xmlDocument.rootElement) {
                // Throw exception than invalid xml string provided
                throw new Error('Invalid XML');
            }
            element = xmlDocument;
        } else {
            element = xml2js(xmlFile.fileContent) as Element;
        }
    } catch (error) {
        log(logger, {
            severity: LogSeverity.Error,
            message: i18next.t('XMLPARSEFAILURE', { error: getErrorMessage(error) }),
            location: {
                path: xmlFile.dataSourceUri
            }
        });
    }
    return element;
}
