import type { Availability } from '..';

export enum LayoutType {
    Compact = 'Compact',
    CompactVisual = 'CompactVisual'
}

export enum InitialLayoutType {
    Compact = 'Compact',
    Visual = 'Visual'
}

/**
 * Visual Filter
 * @isViewNode true
 */
export interface VisualFilter {
    /**
     * Defines where the selection field should be shown.
     * Default: it will be shown by default.
     * Adaptation: it will initially not be shown in the table but be available via end user adaptation.
     * Hidden: the selection field is neither available in the table nor in adaptation.
     * @artifactType Manifest
     */
    availability?: Availability;
    /**
     * The valuelist annotation corresponding to the visual filter representation.
     * @artifactType Manifest
     * @hidden true
     */
    visualFilterValueList?: string;
}

export type SelectionField = {};

/**
 * Filter Fields
 * @isViewNode true
 */
export interface SelectionFields {
    [key: string]: SelectionField;
}

/**
 * Compact Filters
 * @isViewNode true
 */
export interface CompactFilters {
    [key: string]: SelectionField;
}

/**
 * Visual Filters
 * @isViewNode true
 */
export interface VisualFilters {
    [key: string]: VisualFilter;
}

/**
 * Filter Bar
 * @isViewNode true
 */
export interface FilterBar {
    /**
     * To disable the filter bar, set hideFilterBar to true
     * @artifactType Manifest
     */
    hideFilterBar?: boolean;

    /**
     * @isViewNode true
     */
    selectionFields?: SelectionFields | CompactFilters;

    /**
     * @isViewNode true
     */
    visualFilters?: VisualFilters;

    /**
     * Specifies the default filter mode on initial load. Possible values: "Visual" and "Compact"
     * @artifactType Manifest
     */
    initialLayout?: InitialLayoutType;

    /**
     * Possible Values: "Compact" (if only compact filter is present) and "CompactVisual" if both compact and visual filters are present
     * @artifactType Manifest
     */
    layout?: LayoutType;
}
